.input{
    width: 15rem;
    height: 10rem;
    resize: none;
    /* font-size: larger; */
    margin: 1% 43%;
    border: 2px solid #222;
    border-radius: 15px;
    background-color: white;
    position: relative;
}

.button{
    width: 14rem;
    height: 4rem;
    resize: none;
    font-size: larger;
    margin: 1% 44%;
    /* margin-top: 10px; */
    border: none;
    border-radius: 5px;
    background-color: rgb(5, 179, 5);
    color: #fff;
    text-shadow: 1px 1px #444;
    font-size: 18px;
    cursor: pointer;
}